/* App container */
.app {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background: linear-gradient(135deg, #6dd5ed, #2193b0); /* Gradient background */
  min-height: 100vh;
  font-family: 'Arial', sans-serif;
  color: #333;
  transition: background-color 0.5s ease-in-out;
}

/* Responsive layout for smaller screens */
@media (max-width: 768px) {
  .app {
    flex-direction: column;
    align-items: center;
  }

  .employee-list,
  .team-list {
    width: 90%;
    margin-bottom: 20px;
  }
}

/* Employee list container (All Employees) */
.employee-list {
  width: 45%;
  padding: 20px;
  border: 2px solid #ddd;
  border-radius: 12px;
  background-color: #efe8d0; /* Light yellow background for All Employees */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Team list container (Team Members) */
.team-list {
  width: 45%;
  padding: 20px;
  border: 2px solid #ddd;
  border-radius: 12px;
  background-color: #e8f8f5; /* Light greenish-blue background for Team Members */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Hover effect to elevate the containers */
.employee-list:hover,
.team-list:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

/* Title styling */
.employee-list h2,
.team-list h2 {
  text-align: center;
  color: #1f1616;
  font-size: 2.2rem;
  margin-bottom: 20px;
  text-decoration: underline;
  letter-spacing: 1px;
}

/* Employee item */
.employee-item,
.team-member {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 6px;
  background-color: #5a7582;
  transition: background-color 0.3s ease, transform 0.3s ease;
  font-size: 1.1rem;
}

/* Hover effect on employee item */
.employee-item:hover {
  background-color: #b3e5fc;
  transform: scale(1.03);
}

/* Disabled item style */
.disabled {
  color: grey;
  background-color: #eeeeee;
  text-decoration: line-through;
  cursor: not-allowed;
}

/* Button styling */
button {
  margin-left: 10px;
  padding: 8px 18px;
  font-size: 0.95rem;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  color: white;
  background-color: #00bfff;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

/* Hover effect for buttons */
button:hover {
  background-color: #007bbb;
  transform: scale(1.08);
}

/* Add button specific styling */
.add-btn {
  background-color: #28a745; /* Bright green for add */
}

.add-btn:hover {
  background-color: #218838;
}

/* Remove button specific styling */
.remove-btn {
  background-color: #ff4d4d; /* Bright red for remove */
}

.remove-btn:hover {
  background-color: #d90000;
}

/* Sort button styling */
.sort-btn {
  width: 100%;
  padding: 12px;
  background-color: #ffcc00; /* Bright yellow for sort */
  color: #333;
  border: none;
  border-radius: 6px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

/* Hover effect for sort button */
.sort-btn:hover {
  background-color: #ff9900;
  transform: scale(1.05);
}

/* Team list footer */
.team-footer {
  margin-top: 20px;
  text-align: center;
  font-size: 1.2rem;
  color: #333;
  font-weight: bold;
  padding: 15px 0;
  background-color: #f0f0f0;
  border-radius: 6px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}
